<template>
	<div class='msite'>
		<HeaderTop :title="address.name">
			<router-link slot="search" to="/search" class="header_search">
				<i class="iconfont icon-sousuo"></i>
			</router-link>
			<router-link slot="login" :to="userInfo._id ? '/userinfo' :'/login'" class="header_login">
				<span class="header_login_text" v-if="!userInfo._id">|</span>
				<span class="header_login_text" v-else><i class="iconfont icon-person"></i></span>	
					
			</router-link>
		</HeaderTop>
		<!--  -->
		<nav class="msite_nav border-1px">
			<div class="swiper-container">
				<div class="swiper-wrapper" v-if="categorys.length">
				    <div class="swiper-slide" v-for="(categorys,index) in categorysArr" :key='index'>
						<a href="javascript:" class="link_to_food" v-for="(category,index) in categorys" :key='index'>
							<div class="food_container">
								 <img :src="uh+category.image_url">
							</div>
							<span>{{category.title}}</span>
						</a>
						
					</div>

				</div>
			    <img src="./images/nav/msite_back.svg" alt="" v-else>
			<!-- Add Pagination -->
				<div class="swiper-pagination"></div>
			</div>
		</nav>
		<!--  -->
		<div class="msite_shop_list border-1px">
			<div class="shop_header">
				<i class="iconfont icon-xuanxiang"></i>
				<span class="shop_header_title"></span>
			</div>
			<ShopList/>
		</div>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import Swiper from 'swiper'
	import 'swiper/dist/css/swiper.min.css'
	import HeaderTop from '../../components/HeaderTop/HeaderTop.vue'
	import ShopList from '../../components/ShopList/ShopList.vue'
	
	export default{
		components:{
			HeaderTop,
			ShopList
		},
		data(){
			return{
				tit:'da',
				uh:'https://fuss10.elemecdn.com'
			}
		},
		mounted(){
			
			this.$store.dispatch('getCategorys')
			this.$store.dispatch('getShops')
			
		},
		 computed:{
				 ...mapState(['address','categorys','userInfo']),
				 categorysArr(){
						const {categorys} = this
						const arr=[]
						let minArr=[]
						categorys.forEach(c=>{
							if(minArr.length===8){
							minArr=[]
							}
							if(minArr.length===0){
								arr.push(minArr)
							}
							
							minArr.push(c)
							
						})
						return arr
					 }
		},
		watch:{
			categorys(value){
// 				setTimeout(()=>{
// 					new Swiper('.swiper-container',{
// 						loop:true,
// 						pagination:{
// 							el:'.swiper-pagination'
// 						}
// 					})
// 				},100)
            this.$nextTick(()=>{
				new Swiper('.swiper-container',{
										loop:true,
										pagination:{
											el:'.swiper-pagination'
										}
									})
			})
		}
		}
	}
</script>

<style lang='stylus' rel='stylesheet/stylus'>
@import "../../common/stylus/mixins.styl"
	.msite
		width 100%
		.header
			background-color #02a774
			position fixed
			z-index 100
			left 0
			top 0
			width 100%
			height 45px
			.header_search
				position absolute
				left 15px
				top 50%
				transform translateY(-50%)
				width 10%
				height 50%
				.icon-sousuo
					font-size 25px
					color #fff
			.header_title
				position absolute
				top 50%
				left 50%
				transform translate(-50%, -50%)
				width 50%
				color #fff
				text-align center
				.header_title_text
					font-size 20px
					color #fff
					display block
			.header_login
				font-size 14px
				color #fff
				position absolute
				right 15px
				top 50%
				transform translateY(-50%)
				.header_login_text
					color #fff
		.msite_nav
			bottom-border-1px(#e4e4e4)
			margin-top 45px
			height 200px
			background #fff
			.swiper-container
				width 100%
				height 100%
				.swiper-wrapper
					width 100%
					height 100%
					.swiper-slide
						display flex
						justify-content center
						align-items flex-start
						flex-wrap wrap
						.link_to_food
							width 25%
							.food_container
								display block
								width 100%
								text-align center
								padding-bottom 10px
								font-size 0
								img
									display inline-block
									width 50px
									height 50px
							span
								display block
								width 100%
								text-align center
								font-size 13px
								color #666
				.swiper-pagination
					>span.swiper-pagination-bullet-active
						background #02a774
		.msite_shop_list
			top-border-1px(#e4e4e4)
			margin-top 10px
			background #fff
			.shop_header
				padding 10px 10px 0 10px
				.shop_icon
					margin-left 5px
					color #999
				.shop_header_title
					color #999
					font-size 14px
					line-height 20px

</style>



